<template>
  <div class="jubao-layout">
    <header class="jubao-content-topbar clearfix">
      <a class="jubao-content-back" @click="goPrePage()">
      </a>
      <h2 class="jubao-title">举报</h2>
      <a  id="jubao-content-more" class="jubao-content-more" @click="toshare()"></a>
    </header>
    <main class="jubao-main">
      <section class="jubao-line">

      </section>
      <section class="jubao-content-box">
        <div class="who-title">
          <span>举报</span><span class="who-name">{{who}}</span><span>的评论：</span>
        </div>
        <div class="who-title who-content-box">
          <span class="who-name">{{who}}:</span>
          <span class="jubao-ucontent">{{comment}}!</span>
        </div>
        <div class="jubao-choice">
          <div class="jubao-left">
            <mt-radio
              v-model="value"
              :options="options">
            </mt-radio>
          </div>
          <div class="jubao-right">
            <mt-radio
              v-model="value"
              :options="optionsRight">
            </mt-radio>
          </div>
        </div>
        <div class="another-choice">
          <input type="text" class="another-content" placeholder="请填写其他举报理由" v-model="inputContent">
        </div>
        <a class="jubao-btn" @click="juClick($event)">举报</a>
      </section>
    </main>
  </div>
</template>
<script>
  import { Radio } from 'mint-ui';
  import Api from '../../../src/api/api';
  import scriptFilter from '../../utils/filterScript'
  export default{
      data(){
          return{
            options:[],
            optionsRight:[],
            value:'',
            who:'',
            comment:'',
            commentId:-1,
            juBaoContent:'',
            inputContent:''
          }
      },
      mounted(){
        this.options = [
          {
            label: '人身攻击我',
            value: '人身攻击我',
          },
          {
            label: '违法信息',
            value: '违法信息'
          },
          {
            label: '其他',
            value: '其他'
          }
        ];
        this.optionsRight = [
          {
            label: '淫秽色情',
            value: '淫秽色情'
          },
          {
            label: '威胁诽谤',
            value: '威胁诽谤'
          }
        ];
        this.commentId = this.$route.params.commentId;
        this.who = ` ${this.$route.params.who} `;
        this.comment = this.$route.params.content;
      },
      methods:{
        goPrePage(){
          window.history.go(-1);
        },
        juClick($event){
            const _this  = this;
            let temStr = _this.juBaoContent;
            if(scriptFilter(temStr)){
                this.showMsg("内容不能包含奇怪字符!");
                return;
            }
            if(temStr.length>=1){
                if(temStr==='其他'){
                    temStr = _this.inputContent;
                }
                this.$http.post(Api.JUBAONEWS,{
                  content:temStr,
                  comment_id:_this.commentId,
                  status:0
                }).then((res)=>{
                  history.back(-1);
                },(err)=>{
                  _this.showMsg('你已经提交过举报理由了，不能再次提交了!');
                  _this.$store.dispatch('hideLoading');
                });

            }else{
                _this.showMsg('请先选择举报理由');
            }

            $event.preventDefault();
        },
        showMsg(str){
          this.$toast({
            message: str,
            duration: 2000,
          });
        },
      },
      watch:{
          value(newV,oldV){
              this.juBaoContent = newV;
          }
      }
  }
</script>
<style scoped>
  @import "../../../src/assets/css/jubao.css";
</style>



